.App {
  display: grid;
  grid-template-areas: "h h h" "c c c";
  width: 100vw;
}

.visualization-selectors {
  grid-area: c;
  padding: 100px;
}

header {
  grid-area: h;
  position: absolute;
  top: 0;
  left: 0;
  background: #c7f5ff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  height: 64px;
  width: 100vw;
}
